* {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  scroll-behavior: smooth;
  background-color: white;
}

h2 {
  font-family: "Raleway", sans-serif;
  font-size: 2.25rem;
}

.container {
  max-width: 1352px;
  margin: 0 auto;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}

a {
  text-decoration: none;
  cursor: pointer;
}

.header {
  height: 70px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.header__container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logo {
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  color: #08a6e4;
  padding-left: 130px;
}
@media (max-width: 1150px) {
  .header .logo {
    padding-left: 25px;
  }
}
.header .nav__element {
  padding: 0 26px;
}
.header .nav__element:last-child {
  padding-right: 0;
}
.header .nav__link {
  font-family: "Open Sans", sans-serif;
  color: #a5a5a5;
  font-size: 0.6875rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1875rem;
  padding: 15px 20px 25px;
  border-bottom: 4px solid transparent;
}
.header .nav__link:hover {
  color: #000000;
  border-color: #08a6e4;
}
.header__nav {
  display: flex;
}
@media (max-width: 1150px) {
  .header__nav {
    flex-wrap: wrap;
  }
  .header__nav .nav__link {
    font-size: 0.6rem;
  }
  .header__nav .nav__link:hover {
    color: #000000;
    border-color: transparent;
  }
}
@media (max-width: 780px) {
  .header__nav {
    position: absolute;
    left: 0;
    top: 71px;
    flex-direction: column;
    background-color: white;
    width: 100%;
    padding-bottom: 20px;
    display: none;
  }
  .header__nav .nav__element {
    padding: 0;
  }
  .header__nav .nav__link {
    display: block;
    padding: 15px 20px;
    font-size: 0.6875rem;
  }
}

.menu__btn {
  display: none;
  position: absolute;
  top: -9999px;
  left: -9999px;
  opacity: 0;
}
@media (max-width: 780px) {
  .menu__btn:checked ~ .header__nav {
    display: flex;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
.menu__toggle {
  display: none;
}
.menu__toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: #08a6e4;
  transition: 0.3s all ease-in-out;
}
.menu__toggle span:last-child {
  margin-bottom: 0;
}
@media (max-width: 780px) {
  .menu__toggle {
    display: block;
  }
}

.opening {
  overflow: hidden;
}

.opening__banner {
  margin-top: 130px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  overflow: hidden;
}
@media (max-width: 780px) {
  .opening__banner {
    margin-top: 100px;
  }
}

.opening__title {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  color: black;
  margin: 20px 0px;
}
@media (max-width: 780px) {
  .opening__title {
    font-size: 2rem;
  }
}

.opening__text {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: #000000;
}
@media (max-width: 780px) {
  .opening__text {
    padding: 0px 70px;
    text-align: center;
    line-height: 20px;
  }
}

.opening__button {
  margin-top: 38px;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  color: #ffffff;
  width: 440px;
  height: 67px;
  background-color: #08a6e4;
  border-radius: 40px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
@media (max-width: 780px) {
  .opening__button {
    width: 310px;
    margin-top: 28px;
    margin-bottom: 51px;
  }
}

@media (max-width: 780px) {
  .opening__img {
    width: 220%;
  }
}

.statistics {
  margin-top: 100px;
}
@media (max-width: 780px) {
  .statistics {
    margin-top: 50px;
  }
}
.statistics__main {
  display: flex;
  flex-direction: column;
  max-width: 800px;
  margin: 100px auto 120px;
}
.statistics__title {
