    padding: 0px 70px;
    text-align: center;
    line-height: 35px;
    font-size: 1.875rem;
  }
}
.statistics__box-container {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  margin-top: 100px;
}
@media (max-width: 780px) {
  .statistics__box-container {
