}
.benefits__single-notebook .benefits__image::before {
  content: "";
  background-color: #fb3b64;
  width: 180px;
  height: 180px;
  border-radius: 41px;
  display: block;
  position: absolute;
  right: 0;
  top: -40px;
  transform: rotate(45deg);
}
@media (max-width: 1150px) {
  .benefits__single-notebook .benefits__image::before {
    width: 110px;
    height: 110px;
    border-radius: 47px;
    top: -30px;
    right: -20px;
  }
}
.benefits__single-notebook img {
  width: 386px;
}
@media (max-width: 1150px) {
  .benefits__single-notebook img {
    width: 192px;
  }
}
.benefits__single-iphone {
  flex-direction: row-reverse;
}
.benefits__single-iphone .benefits__image {
  margin-left: 70px;
  margin-top: 12px;
}
@media (max-width: 1150px) {
  .benefits__single-iphone .benefits__image {
    margin-top: 10px;
    margin-left: 0px;
  }
}
.benefits__single-iphone .benefits__image::before {
  content: "";
  background-color: #08a6e4;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  display: block;
  position: absolute;
}
@media (max-width: 1150px) {
  .benefits__single-iphone .benefits__image::before {
    width: 132px;
    height: 132px;
    border-radius: 50%;
    top: 20px;
  }
}
.benefits__single-iphone img {
  width: 426px;
  margin-top: 29px;
  margin-bottom: 13px;
}
@media (max-width: 1150px) {
  .benefits__single-iphone img {
    width: 175px;
  }
}
.benefits__text {
  display: flex;
  flex-direction: column;
  margin: 90px 36px;
  max-width: 415px;
}
@media (max-width: 1150px) {
  .benefits__text {
    margin: 30px 22px;
  }
}
.benefits__text::after {
